<div class="hello"><router-link v-if="clickable" :to="'/flag/'+ (h.uid || h.idx)"><div class="image"><h3 class="ui header"><img class="main" :src="getIcon(h)" alt="^_^">{{h.name}}<span v-if="h.learner_birth">-<span v-html="toAge(h.learner_birth)"></span></span><span v-if="h.child_birth">(</span><span v-if="h.child_birth2">{{toAge(h.child_birth2)}} ~</span><span v-if="h.child_birth">{{toAge(h.child_birth)}} )</span></h3></div><div class="content"><p class="description gray"> <span v-html="highlight(h.address, mySearch)"></span>&nbsp;&nbsp; - {{countDateDiff(h.lastUpdate)}}</p><p class="description" v-if="h.learner_habit" v-html="' ' + highlight(h.learner_habit, mySearch)"></p><p class="description" v-if="h.share" v-html="' ' + highlight(h.share, mySearch)"></p><p class="description" v-if="h.ask" v-html="' ' + highlight(h.ask, mySearch)"></p><p class="description" v-if="h.price" v-html="' ' + highlight(h.price, mySearch)"></p></div><div class="content" v-if="h.connect_me &amp;&amp; full"><div class="ui divider"></div><p class="descrtpion" v-if="isEmail(h.connect_me)">email <!--a(@click.stop="goto(h.connect_me)") {{h.connect_me}}--><a @click.stop="gotoMail(h.connect_me)">{{h.connect_me}}</a></p><p class="descrtpion" v-else> {{h.connect_me}}</p><p class="descrtpion" v-if="h.freetime"> {{h.freetime}}</p></div><div class="content"><div class="ui divider"></div><p v-if="h.site"><a @click.stop="goto(h.site)"><img :src="'https://www.google.com/s2/favicons?domain=' + h.site" title="" alt=""><i class="right arrow icon"></i></a></p><p v-if="h.site2"><a @click.stop="goto(h.site2)"><img :src="'https://www.google.com/s2/favicons?domain=' + h.site2" title="" alt=""><i class="right arrow icon"></i></a></p></div><div class="content"><div class="ui divider"></div><p class="descrtpion" v-if="!full"><span class="text" v-html="highlight(part(h.note), mySearch) + '...'"></span><br></p><p class="descrtpion" v-else><span class="text" v-html="highlight(h.note, mySearch)"></span><br></p></div></router-link><div v-else :to="'/flag/'+ (h.uid || h.idx)"><div class="image"><h3 class="ui header"><img class="main" :src="getIcon(h)" alt="^_^">{{h.name}}<span v-if="h.learner_birth">-<span v-html="toAge(h.learner_birth)"></span></span><span v-if="h.child_birth">(</span><span v-if="h.child_birth2">{{toAge(h.child_birth2)}} ~</span><span v-if="h.child_birth">{{toAge(h.child_birth)}} )</span></h3></div><div class="content"><p class="description gray"> <span v-html="highlight(h.address, mySearch)"></span>&nbsp;&nbsp; - {{countDateDiff(h.lastUpdate)}}</p><p class="description" v-if="h.learner_habit" v-html="' ' + highlight(h.learner_habit, mySearch)"></p><p class="description" v-if="h.share" v-html="' ' + highlight(h.share, mySearch)"></p><p class="description" v-if="h.ask" v-html="' ' + highlight(h.ask, mySearch)"></p><p class="description" v-if="h.price" v-html="' ' + highlight(h.price, mySearch)"></p></div><div class="content" v-if="h.connect_me &amp;&amp; full"><div class="ui divider"></div><p class="descrtpion" v-if="isEmail(h.connect_me)">email <!--a(@click.stop="goto(h.connect_me)") {{h.connect_me}}--><a @click.stop="gotoMail(h.connect_me)">{{h.connect_me}}</a></p><p class="descrtpion" v-else> {{h.connect_me}}</p><p class="descrtpion" v-if="h.freetime"> {{h.freetime}}</p></div><div class="content"><div class="ui divider"></div><p v-if="h.site"><a @click.stop="goto(h.site)"><img :src="'https://www.google.com/s2/favicons?domain=' + h.site" title="" alt=""><i class="right arrow icon"></i></a></p><p v-if="h.site2"><a @click.stop="goto(h.site2)"><img :src="'https://www.google.com/s2/favicons?domain=' + h.site2" title="" alt=""><i class="right arrow icon"></i></a></p></div><div class="content"><div class="ui divider"></div><p class="descrtpion" v-if="!full"><span class="text" v-html="highlight(part(h.note), mySearch) + '...'"></span><br></p><p class="descrtpion" v-else><span class="text" v-html="highlight(h.note, mySearch)"></span><br></p></div></div><div class="filler"></div><div class="ui bottom attached stackable buttons" :class="{vertical: !full || thin}"><div class="ui green button" @click="addBook(h.uid || h.idx)" v-if="uid &amp;&amp; (!book || book.indexOf(h.uid || h.idx) == -1)"><i class="book icon"></i></div><div class="ui red button" @click="removeBook(book.indexOf(h.uid || h.idx))" v-else-if="uid &amp;&amp; book &amp;&amp; book.indexOf(h.uid || h.idx) &gt; -1"><i class="book icon"></i></div><!-- router-link.ui.blue.basic.button(to="/profile" v-else)i.user.icon
